.bg-image-div {
  height: 100%;
  width: 100%;
  background: url("../src/images/bgimageresized.jpg");
  height: 100vh;
  width: 100vw;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: fixed;
}

.main-divs
{
    position: relative;
}
.main-left-div {
    float: left;
    height: 100vh;
    width: 45vw;
    background: rgba(0, 100, 0, .7);
}
.main-right-div {
    float: right;
    height: 100vh;
    width: 54.99vw;
    background-color: rgba(0, 0, 0, 0.5);
}